import flatpickr from 'flatpickr';
import { Ukrainian } from 'flatpickr/dist/l10n/uk.js';
import { Czech } from 'flatpickr/dist/l10n/cs.js';
import { Russian } from 'flatpickr/dist/l10n/ru.js';

const locales = {
  uk: Ukrainian,
  cs: Czech,
  ru: Russian,
};

const userLang = navigator.language || navigator.userLanguage;
const langCode = userLang.split('-')[0]; // –æ—Ç—Ä–∏–º—É—î–º–æ, –Ω–∞–ø—Ä–∏–∫–ª–∞–¥, "uk"

const selectedLocale = locales[langCode] || 'default'; // default = –∞–Ω–≥–ª—ñ–π—Å—å–∫–∞

const fp = flatpickr(document.querySelector('.input-date-time'), {
  locale: selectedLocale,
  enableTime: true,
  time_24hr: true, // –ø–æ–∫–∞–∑—É—î 24-–≥–æ–¥–∏–Ω–Ω–∏–π —Ñ–æ—Ä–º–∞—Ç (–±–µ–∑ AM/PM)
  // altInput: true,
  altFormat: 'd.m.Y H:i', // –ø—Ä–∏–∫–ª–∞–¥: 24.05.2025 14:30
  dateFormat: 'Y-m-d H:i', // —Ñ–æ—Ä–º–∞—Ç, —è–∫–∏–π –∑–±–µ—Ä—ñ–≥–∞—î—Ç—å—Å—è —É value
  minDate: 'today',
  position: 'above',
});
